// Import Swiper and modules
import {
  Swiper,
  Navigation,
  Pagination,
  Scrollbar
} from "swiper/dist/js/swiper.esm";
import "bootstrap/js/dist/modal";

// Install modules
Swiper.use([Navigation, Pagination, Scrollbar]);

const certificationSlider = new Swiper(".certification-slider", {
  slidesPerView: "3",
  // centeredSlides: true,
  spaceBetween: 30,
  // Navigation arrows
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev"
  },
  breakpoints: {
    // when window width is <= 320px
    767: {
      slidesPerView: 2,
      spaceBetween: 20
    },
    575: {
      slidesPerView: 1,
      spaceBetween: 10
    }
  }
});

const portfolioSlider = new Swiper(".portfolio-slider", {
  slidesPerView: "2",
  slidesOffsetBefore:
    ($(window).width() - $(".portfolio-slider .container").width()) / 2 + 200,
  spaceBetween: 30,
  loop: true,
  pagination: {
    el: ".swiper-pagination",
    type: "custom",
    renderCustom: (swiper, current, total) =>
      `<span class="swiper-pagination__current">${
        current < 10 ? `0${current}` : current
      }</span>/<span class="swiper-pagination__total">${
        total < 10 ? `0${total}` : total
      }</span>`
  },
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev"
  },
  breakpoints: {
    1200: {
      slidesPerView: 1.5
    },
    767: {
      slidesOffsetBefore: 100,
      slidesPerView: 1,
      spaceBetween: 20
    }
  }
});
